import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
import Menu from '../components/Menu'
import Page from '../components/Page'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/menu/',
      name: 'Menu',
      component: Menu,
    },
    {
      path: '/page/',
      name: 'Page',
      component: Page,
    },
  ]
})



